html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
    font-size: 48px;
    margin-bottom: 40px;
    margin-top: 20px;
}

h2 {
    font-size: 30px;
    margin-bottom: 20px;
    margin-top: 20px;
}

.wrapper {
    max-width: 1200px;
    width: auto;
    min-width: 400px;
    margin: 0 auto;
    padding: 0px 20px;
}

.filters {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}

.singleFilter {
    width: 45%;
}

.tableContainer {
    overflow-x: auto !important;
}

.table {
    width: auto;
    border: none;
    margin-bottom: 20px;
    border-collapse: separate;
}
.table thead th {
    font-weight: bold;
    text-align: left;
    border: none;
    padding: 10px 15px;
    background: #ededed;
    font-size: 14px;
    border-top: 1px solid #ddd;
}

.table th:nth-child(1),
.table td:nth-child(1) {
    width: 10%;
}
.table th:nth-child(2),
.table td:nth-child(2) {
    width: 20%;
}
.table th:nth-child(3),
.table td:nth-child(3) {
    width: 20%;
}
.table th:nth-child(4),
.table td:nth-child(4) {
    width: 20%;
}
.table tr th:first-child,
.table tr td:first-child {
    border-left: 1px solid #ddd;
}
.table tr th:last-child,
.table tr td:last-child {
    border-right: 1px solid #ddd;
}

.table tbody td {
    text-align: left;
    border: none;
    padding: 10px 15px;
    font-size: 14px;
    vertical-align: middle;
}
.table tbody tr:nth-child(even) {
    background: #f8f8f8;
}
.table tbody tr:last-child td {
    border-bottom: 1px solid #ddd;
}

.todos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card-header {
    font-weight: bold !important;
    font-size: 17px;
}

.list-group-item {
    min-height: 60px !important;
}

.taskBlock {
    font-weight: bold;
}

.lds-dual-ring {
    display: block;
    width: 80px;
    height: 80px;
    margin: 0 auto;
}
.lds-dual-ring:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: rgb(41, 22, 22) transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
